	module.exports = {"data":{"markdownRemark":{"html":"<p>Apropos to this course on learning how to learn, I learned that drawing things can help people remember.</p>\n<h2>Gradualism</h2>\n<p>While our hearts may want to 'rip and replace' bad habits, we need to go slow and build new habits out of the debris of the existing not so great habits.</p>\n<h3>Overwriting bad habits</h3>\n<p>We have to change our reactions to cues to start to change old habits. <img src=\"/images/zombie-pull.jpg\" alt=\"Zombie Harnessing\">. We want to systematically look at 4 aspects of cues to really understand where we go off the rails: location, time, our current emotion and reactions. We want to ID these cues that trigger procrastination and reorient them towards productive uses. If certain locations or times are bad for productivity, let's avoid them. Emotion can mean noting which mental states can lend themselves more to procrastinating. Reactions mean noticing when stimulus A leads to effect B that we don't want.</p>\n<p>Routines need to be broken, too. For example, if we sit down, open a book, and check the smartphone, that is a bad routine. We need to recognize our bad routines and have a plan to counter them. For example, leave the phone in the car, or pick a place in the library that is quiet and stimulus free.</p>\n<p>Rewards are also important. Why are we procrastinating? What rewards are we getting now? Can we reframe this somehow to give ourselves rewards? Anything from bargaining with ourselves ('if I study now, I will watch TV tonight'), or  buying ourselves treats.</p>\n<p>Lastly, we need to believe in the system and that we can change. We have to actually, consciously, want to change. We need this to power through the first few tries of making a new habit, because we may stumble, and succumb to temptation. I know I did in writing this post, I have been on and off goofing off for an hour.</p>\n<h3>Taskmastering</h3>\n<p>Write weekly tasks in a planner journal and then write daily to-dos the night before, immediately before you go to bed. <img src=\"/images/zombie-walker.jpg\" alt=\"Zombie Walker\"> This helps our inner zombie know what it is you expect, and frees up working memory for more useful things, as our mind mulls over our activities while we sleep. Long term items should be process oriented and provided chunks of time - instead of \"I need to finish Climate Cents website\" which is a fairly involved endeavor, I ought to be putting block of time dedicated to that every day, which makes things achievable.</p>\n<p>Short term items should be treated as product-oriented only if you can ship them today. Even then, break down product to-dos with Pomodoro sprints. In the daily to-do list keep it to, say, 6 items to be realistic.</p>\n<p>Think about using physical activity for the 5 minute breaks. This can break up uhealthy long stretches of sitting and allow the brain to more fully go into diffuse mode and truly break.</p>\n<p>Also set up a goal time for quitting. This is important psychologically so you reward yourself, and it can help make you more productive because you are as committed to leisure time and the renewal that brings.</p>\n<h3>Eat Your Frogs First</h3>\n<p>Do your least favorite task first. Get it over with. <img src=\"/images/zombie-faceeat.png\" alt=\"Zombie Face Eater\"> Perhaps start by waking up in the morning and doing a quick Pomodoro session, focusing on a task you like the least. It is a good way to get a quick win.</p>\n<h3>Neural Scaffolding</h3>\n<p>Building new habits is a daily task.</p>\n<ol>\n<li>Keep a planner journal. Track to do items by week and by day, track progress on what is and is not working, and remember that long term (multi-day) projects should be allocated time (process) not judged on output (product)</li>\n<li>Arrange work in a series of sprints using the Pomodoro.</li>\n<li>Commit to daily routines and tasks. I find that I do best when I schedule things in a calendar.</li>\n<li>Watch for your procrastination cues (location, time, emotion, reaction) and be proactive in reframing or avoiding procrastination triggers</li>\n<li>Gain trust in your new system, which I take to mean play Dodger baseball. Stack up series of hits instead of trying to go yard each time. Incremental progress should build confidence.</li>\n<li>Have a backup plan if you slip up and procrastinate.</li>\n<li>Eat your frogs first. Get the worst stuff out of the way.</li>\n<li>Habits have 4 parts too: Cue, Routine, Belief, Reward.</li>\n<li>Picking a qutting time is as important as anything else in your planning.</li>\n<li>Design rewards for yourself that motivate you.</li>\n<li>Keep your task list for the day achievable.</li>\n</ol>","frontmatter":{"date":"May 13, 2016","path":"/blog/zombie-harnessing/","title":"Zombie Harnessing","type":"blog"}}},"pathContext":{}}

